import React, { Component } from 'react';
import styled from 'styled-components';
import { Scrollbars } from 'react-custom-scrollbars';

import color from '../assets/color';
import SidebarToolbar from './sidebar_toolbar';
import SidebarTab from './sidebar_tab';

const StyledSidebar = styled.section`
  position: absolute;
  z-index: 1;
  width: ${props => props.sidebarWidth || 0}px;
  min-width: 15rem;
  max-width: 20rem;
  height: 100%;
  float: left;
  background: ${ color.black };
`;

const Meow = styled.div`
  height: 100px;
  background: ${color.grey1};
  margin: 10px 0;
`;

export default class Sidebar extends Component{
  constructor(props){
    super(props);
    this.state = {
      mode: "EDIT"
    }
    this.handleSetMode = this.handleSetMode.bind(this);
  }

  handleSetMode(newMode){
    this.setState({
      mode: newMode
    });
  }

  render(){
    return (
      <StyledSidebar>
        <SidebarTab mode={this.state.mode} setMode={this.handleSetMode} />
        <SidebarToolbar mode={this.state.mode} />
        <Scrollbars 
          style={{ 
            width: "100%", 
            height: "calc(100% - 10.5rem)"
          }} 
          renderThumbVertical={({ style, ...props }) => 
            <div {...props} 
              style={{ 
                ...style, 
                backgroundColor: color.grey5, 
                opacity: 0.5, 
                borderRadius: "5px"
              }}
            />}
        >
          
        </Scrollbars>
      </StyledSidebar>
    );
  }
}


